import React, { useState } from "react";
import { FiMenu } from "react-icons/fi";

const HamburgerMenu = ({ menuItems, handleSegmentChange }) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  const handleItemClick = (item) => {
    handleSegmentChange(item); // Trigger the same scrolling behavior
    setIsOpen(false); // Close the menu after selecting an item
  };

  return (
    <div style={{ position: "relative" }}>
      {/* Hamburger Icon */}
      <button
        onClick={toggleMenu}
        style={{
          color: "orange",
          background: "none",
          border: "none",
          fontSize: "24px",
          cursor: "pointer",
        }}
      >
        <FiMenu />
      </button>

      {/* Dropdown Menu */}
      {isOpen && (
        <ul
          style={{
            position: "absolute",
            top: "40px",
            right: "0",
            listStyle: "none",
            margin: "0",
            padding: "10px",
            background: "white",
            boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)",
            borderRadius: "4px",
            zIndex: 1000,
          }}
        >
          {menuItems.map((item, index) => (
            <li
              key={index}
              onClick={() => handleItemClick(item)}
              style={{
                padding: "8px 16px",
                cursor: "pointer",
                borderBottom:
                  index !== menuItems.length - 1 ? "1px solid #ccc" : "none",
              }}
            >
              {item}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

export default HamburgerMenu;
